import React, { useState, useRef, useEffect } from "react";
import {
  StyleSheet,
  Dimensions,
  View,
  Text,
  TouchableOpacity,
  Image,
} from "react-native";
import { Camera } from "expo-camera";
import line from '../assets/line.png';
import { FontAwesome} from '@expo/vector-icons'
import arrow from '../assets/top.png';
import whiteArrow from '../assets/white.png';
import { createStackNavigator } from '@react-navigation/stack';


const WINDOW_HEIGHT = Dimensions.get("window").height;
const closeButtonSize = Math.floor(WINDOW_HEIGHT * 0.032);
const captureSize = Math.floor(WINDOW_HEIGHT * 0.09);


export default function BackCamera( { navigation }) {
  const [hasPermission, setHasPermission] = useState(null);
  const [cameraType, setCameraType] = useState(Camera.Constants.Type.back);
  const [isPreview, setIsPreview] = useState(false);
  const [isCameraReady, setIsCameraReady] = useState(false);
  const [capturedPhoto, setcapturedPhoto] = useState(null);
  const [open, setOpen] = useState(false);

  console.log("pic", isPreview)
  const cameraRef = useRef();
  useEffect(() => {
    (async () => {
      const { status } = await Camera.requestPermissionsAsync()

      setHasPermission(status === "granted");
    })();
  }, []);
  const onCameraReady = () => {
    setIsCameraReady(true);
  };
  const takePicture = async () => {
    if (cameraRef.current) {
      const options = { quality: 0.5, base64: true, skipProcessing: true };
      const data = await cameraRef.current.takePictureAsync(options);
      const source = data.uri;
      if (source) {
        await cameraRef.current.pausePreview();
        setcapturedPhoto(source);
        setIsPreview(true);
        setOpen(true);
        console.log("picture source", source);
      }
    }
  };

  const switchCamera = () => {
    if (isPreview) {
      return;
    }
    setCameraType((prevCameraType) =>
      prevCameraType === Camera.Constants.Type.back
        ? Camera.Constants.Type.front
        : Camera.Constants.Type.back
    );
  };
  const cancelPreview = async () => {
    setOpen(false);
    setcapturedPhoto(null);
    // await cameraRef.current.resumePreview();
    setIsPreview(false);
   
    // setVideoSource(null);
  };
  const renderCancelPreviewButton = () => (
    <View style={{ flex: 0.4,backgroundColor: '#fff',textAlign: 'center', }}>
     <Text style={{textAlign: 'center',}}> <Image source={whiteArrow} style={{position:'relative',top:-6,width:24,height:15}}/> </Text>
 
   <Text style={styles.buttonTextTop}>画像がブレたり、見切れている場合は
 </Text>
 <Text style={styles.buttonTextTopBr}> 
再撮影をしてください。</Text>
   <View style={{ width:280,justifyContent: "center",position:'relative',top:-10, alignItems: "center",textAlign: 'center',margin:'auto' }}>
    <TouchableOpacity style={styles.button} onPress={() => navigation.navigate('SideCamera')}>
      <Text style={styles.buttonText}> これを使う </Text>
     </TouchableOpacity>
      <TouchableOpacity onPress={cancelPreview}   style={styles.button2} >
      <Text style={styles.buttonText2}> 再撮影する </Text>
     </TouchableOpacity>
      </View>
    </View>
  );

  const renderCaptureControl = () => (
    <View style={styles.control}>
      <TouchableOpacity disabled={!isCameraReady} onPress={switchCamera}>
        <Text style={styles.refresh}><FontAwesome name="refresh" size={17} color="#fff"/></Text>
      </TouchableOpacity>
      <TouchableOpacity
        activeOpacity={0.7}
        disabled={!isCameraReady}
        onPress={takePicture}
        style={styles.capture}
      >
       <FontAwesome  style={styles.cameraicon} name="camera" size={20} color="#46b0a9"/>
      </TouchableOpacity>
        
    </View>
  );
  if (hasPermission === null) {
    return <View />;
  }
  if (hasPermission === false) {
    return <Text style={styles.text}>No access to camera</Text>;
  }
  return (
    <View style={styles.container}>
   
   <View style={styles.header}>
        <Text style={styles.header_text}>PICK UP</Text>
        <Text style={{ textAlign: 'center',  color: '#84807c',position: 'relative',top: -10,marginLeft:135,}}>JA / EN</Text>
     <View  style={{alignItems: 'center',position:"relative",top:-1,paddingBottom:10,}}>
                <Image source={line} style={{width: 18, height: 2}}/>
            </View>
    </View>
    <View style={styles.bottomHeader}>
        <View style={{ flex: 1, }}></View>
        <View style={{ flex: 6, paddingTop: 10,textAlign: 'center', }}>
          <Text style={styles.bottomTextSize2}> 本人確認 </Text>
        </View>
        <View style={{ flex: 1, paddingTop: 10 }}>
          <Text style={styles.bottomTextSize2}>X</Text>
        </View>
      </View>
      
    { open == false &&
      <View style={{flex: 1}}>
    <Camera
        ref={cameraRef}
        style={styles.cus}
        type={cameraType}
        flashMode={Camera.Constants.FlashMode.on}
        onCameraReady={onCameraReady}
        onMountError={(error) => {
          console.log("cammera error", error);
        }}
        
      />
      <View style={styles.boxSimple1}>
        <View style={styles.boxSimple2}>
          <Text style={styles.boxSimpleText}>うら面</Text>
        </View>
        
      </View>
   </View>

      }


{!open && 
      <View>

      { !isPreview && renderCaptureControl()}

      </View>
}
 
  
  {
      capturedPhoto && 
      <View style={styles.cus}>
      <Image
        style={styles.cus}
        source={{ uri: capturedPhoto}}
        />
        {isPreview && renderCancelPreviewButton()}
      </View>
  }

{!open && 
      <View style={styles.cameraBottom}>
           <Text style={styles.cameraBottomTextIcon}>
           <Image source={arrow} style={{position:'relative',top:-6,width:24,height:15}}/>
          </Text>
          <Text style={styles.cameraBottomText}>
           
           <FontAwesome name="info-circle" size={12} color="#fff"/> 枠に収まるように撮影をしてください。
          </Text>
      </View>
}
    </View>
  );
}
