import React, { useState, useRef, useEffect } from "react";
import {
  StyleSheet,
  Dimensions,
  View,
  Text,
  TouchableOpacity,
  Image,
  Button
} from "react-native";
import { Camera } from "expo-camera";
import { FontAwesome} from '@expo/vector-icons'
import { Video , AVPlaybackStatus } from "expo-av";
import { createStackNavigator } from '@react-navigation/stack';
import mold1 from '../assets/mold1.png';
import * as Permissions from 'expo-permissions';

const WINDOW_HEIGHT = Dimensions.get("window").height;
const closeButtonSize = Math.floor(WINDOW_HEIGHT * 0.032);
const captureSize = Math.floor(WINDOW_HEIGHT * 0.09);


export default function ThirdVideo( { navigation }) {
  const [hasPermission, setHasPermission] = useState(null);
  const [cameraType, setCameraType] = useState(Camera.Constants.Type.back);
  const [isPreview, setIsPreview] = useState(false);
  const [isCameraReady, setIsCameraReady] = useState(false);
  const [capturedPhoto, setcapturedPhoto] = useState(null);
  const [isVideoRecording, setIsVideoRecording] = useState(false);
  const [videoSource, setVideoSource] = useState(null);
  const [open, setOpen] = useState(false);
  const [status, setStatus] = useState({});

  const cameraRef = useRef();
  const video = useRef(null);
  useEffect(() => {
    (async () => {
      const { status } = await Camera.requestPermissionsAsync()
      const audio = await Permissions.askAsync(Permissions.AUDIO_RECORDING);

      setHasPermission(status === "granted"  && audio.status === 'granted');
    })();
  }, []);
  const onCameraReady = () => {
    setIsCameraReady(true);
  };


  const recordVideo = async () => {
    if (cameraRef.current) {
      try {
        const videoRecordPromise = cameraRef.current.recordAsync();

        if (videoRecordPromise) {
          setIsVideoRecording(true);
          const data = await videoRecordPromise;
          const source = data.uri;
          if (source) {
            setIsPreview(true);
            setOpen(true);
            console.log("video source", source);
            setVideoSource(source);
          }
        }
      } catch (error) {
        console.warn(error);
      }
    }
  };

  const stopVideoRecording = () => {
    if (cameraRef.current) {
      setIsPreview(false);
      setIsVideoRecording(false);
      cameraRef.current.stopRecording();
    }
  };

  const switchCamera = () => {
    if (isPreview) {
      return;
    }
    setCameraType((prevCameraType) =>
      prevCameraType === Camera.Constants.Type.back
        ? Camera.Constants.Type.front
        : Camera.Constants.Type.back
    );
  };


const cancelPreview = async () => {
    // await cameraRef.current.resumePreview();
    setOpen(false);
    setIsPreview(false);
    setVideoSource(null);
  };

  const renderCancelPreviewButton = () => (
    <View style={{ flex: 0.4}}>
    <Text style={styles.buttonTextTop}>枠に収まるように撮影をしてください。</Text>
  <TouchableOpacity onPress={cancelPreview}   style={styles.button2} >
    <Text style={styles.buttonText}> cancel </Text>
   </TouchableOpacity>
  <TouchableOpacity style={styles.button} onPress={() => navigation.navigate('ThirdVideo')} >
    <Text style={styles.buttonText}> 登録する </Text>
   </TouchableOpacity>
   {/* <TouchableOpacity style={styles.button3} onPress={() =>
            status.isPlaying ? video.current.pauseAsync() : video.current.playAsync()
            
          }
          >
       
       
        <Text>{status.isPlaying ? <Text> <FontAwesome name="pause-circle" size={23} color="#000"/></Text> : <Text> <FontAwesome name="play-circle" size={23} color="#000"/></Text> }</Text>
      </TouchableOpacity> */}

   
  </View>
  );

  const renderVideoPlayer = () => (
    <Video
      ref={video}
      source={{ uri: videoSource }}
      shouldPlay={true}
      style={styles.media}
      useNativeControls
      isLooping
      onPlaybackStatusUpdate={status => setStatus(() => status)}

    />
  );

  const renderVideoRecordIndicator = () => (
    <View style={styles.recordIndicatorContainer}>
      <View style={styles.recordDot} />
      <Text style={styles.recordTitle}>{"Recording..."}</Text>
    </View>
  );


  const renderCaptureControl = () => (
    <View style={styles.control}>
      <TouchableOpacity disabled={!isCameraReady} onPress={switchCamera}>
        <Text style={styles.text}>{"Flip"}</Text>
      </TouchableOpacity>
      <TouchableOpacity
        activeOpacity={0.7}
        disabled={!isCameraReady}
        onLongPress={recordVideo}
        onPressOut={stopVideoRecording}
        // onPress={takePicture}
        style={styles.capture}
      />
        
    </View>
  );
  if (hasPermission === null) {
    return <View />;
  }
  if (hasPermission === false) {
    return <Text style={styles.text}>No access to camera</Text>;
  }
  return (
    <View style={styles.container}>
   
   <View style={styles.header}>
        <Text style={styles.header_text}>PICK UP</Text>
        <Text style={{ textAlign: 'center',  color: '#fff',position: 'relative',top: -10,left:65}}>JA / EN</Text>
    </View>
    <View style={styles.bottomHeader}>
        <View style={{ flex: 3, }}></View>
        <View style={{ flex: 6, paddingTop: 10 }}>
          <Text style={styles.bottomTextSize2}>アカウント登録</Text>
        </View>
        <View style={{ flex: 1, paddingTop: 10 }}>
          <Text style={styles.bottomTextSize2}>X</Text>
        </View>
      </View>
      
    { open == false &&
      <View style={{flex: 1}}>
    <Camera
        ref={cameraRef}
        style={styles.cus}
        type={cameraType}
        flashMode={Camera.Constants.FlashMode.on}
        onCameraReady={onCameraReady}
        onMountError={(error) => {
          console.log("cammera error", error);
        }}
        
      />
     
     <View style={styles.boxSimple1}>
        <View style={styles.boxSimple2}>
        <Image source={mold1} style={{width: 380,height:340 }}/>
        </View>
        
      </View>

   </View>

      }


    {!open && 
      <View>

    {!videoSource && !isPreview && renderCaptureControl()}

      </View>
}
 
  
  { videoSource && 
      <View style={styles.cus}>
      {isVideoRecording && renderVideoRecordIndicator()}
        {videoSource && renderVideoPlayer()}
        {isPreview && renderCancelPreviewButton()}
      </View>
 }
      {/* <View style={styles.cameraBottom}>
          <Text style={styles.buttonTextTop}>
          枠に収まるように撮影をしてください。
          </Text>
      </View> */}

    </View>
  );
}
